import { Mark } from '@ali/4ever-cangjie';
import * as React from 'react';
import { theme } from '@ali/we-design';

export interface MentionMarkProps {
  mark: Mark;
}

const style: React.CSSProperties = {
  color: theme.primaryColor,
};

const MentionMark: React.FC<MentionMarkProps> = (props) => {
  const { mark, children } = props;

  return (
    <span data-cangjie-mark data-mark-type={mark.type} style={style}>{children}</span>
  );
};

MentionMark.displayName = 'MentionMark';

export default MentionMark;
